@import './variables';

.blog-container{
  background-color: $dark;
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  .left{
    grid-area: left;
    text-align: left;
    img{
      width: 50%;
    }
    h2{
      @include title
    }
    span{
      @include span
    }
  }
  .right{
    grid-area: right;
    .article{
      display: grid;
      grid-template-columns: 20% 60% 20%;
      grid-template-areas: "date title link";
      border-bottom: .5px solid $secondary-text;
      padding:20px 0;
    }
    .date{
      @include span;
      grid-area: date;
      font-size: .6em;
    }
    .title{
      grid-area: title;
      a{
        color: $primary-text;
        font-size: 1.3em;
        text-decoration: none;
      }
    }
    .link{
      grid-area: link;
      img{
        text-decoration: underline;
        width: 20px;
      }
    }
  }
}