@media only screen and (max-width: 480px) {
  .navbar-container {
    display: none;
  }

  .intro-container {
    background-color: #323646;
    display: flex;
    flex-direction: column;
    padding: 0;
  }
  /* Welcome */
  .welcome-container {
    display: flex;
    color: white;
    flex-direction: column;
    justify-content: space-between;
    .welcome-name h1 {
      text-align: left;
      font-size: 3em;
      padding: 5px;
    }
  }
  .welcome-socials {
    list-style: none;
    margin: 20px 0;
    margin-top: 1rem;
  }
  .welcome-right{
    width:100%;
    span{
      display: none;
    }
  }
}
